// Dynamic import to avoid SSR issues
let nativeAPI: any = null;
let isInitialized = false;

export class ComfyUIAPI {
  static async init() {
    if (!isInitialized && typeof window !== "undefined") {
      try {
        // Dynamically import the ComfyUI API only on client side
        const module = await import("@comfyorg/comfyui-frontend");
        nativeAPI = module.api;
        
        // Determine if we're in dev mode or ComfyUI mode
        const isDev = window.location.port === '3000' || window.location.port === '3001' || 
                      (window.location.hostname === 'localhost' && window.location.port !== '8188');
        const isComfyUI = window.location.pathname.startsWith('/opencut');
        
        let apiHost: string;
        let apiPort: string;
        let wsProtocol: string;
        let httpProtocol: string;
        
        if (isDev) {
          // In dev mode, use proxy endpoints
          // API calls will go through Next.js proxy
          apiHost = window.location.hostname;
          apiPort = window.location.port; // Use current port (3000, 3001, etc.)
          wsProtocol = 'ws';
          httpProtocol = 'http';
          
          // Override API URL to use proxy
          if (nativeAPI.apiURL) {
            nativeAPI.apiURL = (url: string) => {
              // Use relative URLs so they go through Next.js proxy
              return url.startsWith('/') ? url : `/${url}`;
            };
          }
          
          // In dev mode, disable WebSocket to avoid connection errors
          // Next.js proxy cannot handle WebSocket upgrades
          if (nativeAPI.websocketURL) {
            nativeAPI.websocketURL = () => {
              // Return null to disable WebSocket in dev mode
              return null;
            };
          }
          
          // Disable WebSocket reconnection in dev mode
          if (nativeAPI.createSocket) {
            const originalCreateSocket = nativeAPI.createSocket;
            nativeAPI.createSocket = function() {
              console.log('WebSocket disabled in dev mode - using polling instead');
              return null;
            };
          }
          
          // Override socket property to prevent WebSocket creation
          Object.defineProperty(nativeAPI, 'socket', {
            get() {
              return null;
            },
            set(value) {
              // Ignore socket assignment in dev mode
              console.log('WebSocket assignment blocked in dev mode');
            },
            configurable: true
          });
        } else if (isComfyUI) {
          // In ComfyUI mode, connect directly to ComfyUI backend
          apiHost = window.location.hostname;
          apiPort = window.location.port || '8188';
          httpProtocol = window.location.protocol.replace(':', '');
          wsProtocol = httpProtocol === 'https' ? 'wss' : 'ws';
          
          // Override API URL to remove /opencut prefix
          if (nativeAPI.apiURL) {
            nativeAPI.apiURL = (url: string) => {
              const cleanUrl = url.replace(/^\/opencut/, '');
              return `${httpProtocol}://${apiHost}:${apiPort}${cleanUrl}`;
            };
          }
          
          // Override WebSocket URL
          if (nativeAPI.websocketURL) {
            nativeAPI.websocketURL = () => {
              return `${wsProtocol}://${apiHost}:${apiPort}/ws`;
            };
          }
        } else {
          // Default standalone mode
          apiHost = '127.0.0.1';
          apiPort = '8188';
          httpProtocol = 'http';
          wsProtocol = 'ws';
          
          if (nativeAPI.apiURL) {
            nativeAPI.apiURL = (url: string) => {
              return `${httpProtocol}://${apiHost}:${apiPort}${url.startsWith('/') ? url : `/${url}`}`;
            };
          }
          
          if (nativeAPI.websocketURL) {
            nativeAPI.websocketURL = () => {
              return `${wsProtocol}://${apiHost}:${apiPort}/ws`;
            };
          }
        }
        
        // Set API base properties if they exist
        if (nativeAPI.api_base !== undefined) {
          nativeAPI.api_base = isDev ? '' : `${httpProtocol}://${apiHost}:${apiPort}`;
        }
        if (nativeAPI.apiHost !== undefined) {
          nativeAPI.apiHost = `${apiHost}:${apiPort}`;
        }
        
        // Only initialize if not in dev mode, or initialize with restrictions in dev mode
        if (!isDev) {
          nativeAPI.init();
        } else {
          // In dev mode, manually initialize without WebSocket
          console.log('Initializing ComfyUI API in dev mode without WebSocket');
          // Set any necessary properties without calling init() which tries to create WebSocket
          if (nativeAPI.apiBase) {
            nativeAPI.apiBase = '';
          }
        }
        isInitialized = true;
        
        console.log('ComfyUI API initialized:', {
          mode: isDev ? 'dev' : (isComfyUI ? 'comfyui' : 'standalone'),
          apiBase: isDev ? 'proxy' : `${httpProtocol}://${apiHost}:${apiPort}`
        });
      } catch (error) {
        console.error("Failed to initialize ComfyUI API:", error);
      }
    }
  }

  static async checkStatus(): Promise<{
    status: "success" | "error" | "loading";
    message: string;
  }> {
    // Only run on client side
    if (typeof window === "undefined") {
      return {
        status: "error",
        message: "ComfyUI API can only be used in browser",
      };
    }

    try {
      // Initialize if not already done
      await this.init();
      
      if (!nativeAPI) {
        return {
          status: "error",
          message: "ComfyUI API not initialized",
        };
      }
      
      // Try to get system stats from ComfyUI
      await nativeAPI.getSystemStats();
      
      return {
        status: "success",
        message: "Connected to ComfyUI",
      };
    } catch (error) {
      console.error("ComfyUI connection error:", error);
      return {
        status: "error",
        message: "Unable to connect to ComfyUI. Make sure ComfyUI is running.",
      };
    }
  }

  static async getSystemInfo() {
    if (typeof window === "undefined") {
      return null;
    }

    try {
      await this.init();
      if (!nativeAPI) return null;
      
      const stats = await nativeAPI.getSystemStats();
      return stats;
    } catch (error) {
      console.error("Failed to get system info:", error);
      return null;
    }
  }

  static async getModels() {
    if (typeof window === "undefined") {
      return [];
    }

    try {
      await this.init();
      if (!nativeAPI) return [];
      
      // This would fetch available models from ComfyUI
      // The exact method depends on the ComfyUI API structure
      return [];
    } catch (error) {
      console.error("Failed to get models:", error);
      return [];
    }
  }

  static async getSamplers() {
    if (typeof window === "undefined") {
      return [];
    }

    try {
      await this.init();
      if (!nativeAPI) return [];
      
      // This would fetch available samplers from ComfyUI
      // The exact method depends on the ComfyUI API structure
      return [];
    } catch (error) {
      console.error("Failed to get samplers:", error);
      return [];
    }
  }
}

// Check if we're in ComfyUI environment
export function isComfyUIEnvironment(): boolean {
  if (typeof window !== "undefined") {
    // Check if we're running under /opencut path (ComfyUI mode)
    return window.location.pathname.startsWith("/opencut");
  }
  return false;
}

export default ComfyUIAPI;